export default {
  name: 'Label',
  data() {
    return {
      labelStyle: {
        color: this.color,
        background: this.background,
      },
      labelActiveStyle: {
        color: this.colorActive,
        background: this.backgroundActive,
      }
    };
  },
  props: {
    active: {
      type: Boolean,
      default: false,
    },
    color: {
      type: String,
      default: '#343434',
    },
    colorActive: {
      type: String,
      default: '#343434',
    },
    background: {
      type: String,
      default: "#ccc",
    },
    backgroundActive: {
      type: String,
      default: "#ffff33",
    },
    text: {
      type: String,
      default: '不紧急',
    },
    textActive: {
      type: String,
      default: '紧急',
    },
    size: {
      type: String,
      default: 'md',
      validator: function (value) {
        return ['lg', 'md', 'sm', 'xm'].indexOf(value) !== -1
      }
    },
  },
  mounted() { },
  filters: {
    columnClass: (column) => 'column-' + column
  },
  methods: {
    onClick() {
      this.$emit('on-active')
    },
  }
}




