

import Vue from 'vue'
import App from './App'
import router from './router'
import store from './store/'

import 'font-awesome/css/font-awesome.css'
import '@/assets/iconfont/iconfont.css';

import '@/style/common.css';
import 'swiper/dist/css/swiper.css';
import animate from 'animate.css/animate.min.css';
Vue.use(animate)


import { startRequest } from './api'
Vue.prototype.$startRequest = startRequest;

import LoadingPlugin from 'vux/src/plugins/loading/index.js'
import ToastPlugin from 'vux/src/plugins/toast/index.js'
import DatetimePlugin from 'vux/src/plugins/datetime/index.js'
import ConfirmPlugin from 'vux/src/plugins/confirm/index.js'

Vue.use(LoadingPlugin)
Vue.use(ToastPlugin)
Vue.use(DatetimePlugin)
Vue.use(ConfirmPlugin)
const FastClick = require('fastclick');
FastClick.attach(document.body);
Vue.config.productionTip = false;

/* eslint-disable no-new */
new Vue({
el: '#app',
router,
store,
components: { App },
template: '<App/>'
});



