
import dateFormat from 'vux/src/tools/date/format.js'

export default {
name: "timeline",
components: {},
data() {
return {
timeLine: {},
itemList: {}
};
},
mounted() {
this.getTimeLine();
},
methods: {
goDetails(index) {
this.$router.push({
name: "itemDetails",
params: {
syncKey: index
}
});
},
getTimeLine() {
let requestData = this.$store.getters.tokenInfo;
this.$startRequest(
"/item/getTimeLine",
requestData,
res => {
this.timeLine = res.data.timeLine;
this.itemList = res.data.itemList;
},
error => {
if (error.msg) {
this.$vux.toast.text(error.msg, "top");
} else {
this.$vux.toast.text("网络错误", "top");
}
}
);
}
}
};



