
import XCircle from 'vux/src/components/x-circle/index.vue'

export default {
name: "CircleGroup",
data() {
return {};
},
props: {
hasHour: {
type: Boolean,
default: true
},
hour: {
type: Number,
default: 0,
validator: value => value >= 0 && value <= 24
},
minute: {
type: Number,
default: 0,
validator: value => value >= 0 && value <= 60
},
second: {
type: Number,
default: 0,
validator: value => value >= 0 && value <= 60
},
maxHour: {
type: Number,
default: 24,
validator: value => value != 0
},
maxMinute: {
type: Number,
default: 60,
validator: value => value != 0
},
maxSecond: {
type: Number,
default: 60,
validator: value => value != 0
},
anticlockwise: {
type: Boolean,
default: false
}
},
components: {
XCircle
},
mounted() {},
computed: {

cptSecond: function() {
return parseInt((this.second / this.maxSecond) * 100)
},
cptMinute: function() {
return  parseInt((this.minute / this.maxMinute) * 100)
},
cptHour: function() {
if (this.hasHour)
return parseInt((this.hour / this.maxHour) * 100)
else
return 0;
}
}
};



