export default {
  name: 'DigitalClock',
  data() {
    return {};
  },
  props: {
    hasHour: {
      type: Boolean,
      default: true,
    },
    column1: {
      type: Number,
      default: 0,
      validator: function (value) {
        return value >= 0 && value <= 2
      },
    },
    column2: {
      type: Number,
      default: 0,
      validator: function (value) {
        return value >= 0 && value <= 9
      },
    },
    column3: {
      type: Number,
      default: 0,
      validator: function (value) {
        return value >= 0 && value <= 5
      },
    },
    column4: {
      type: Number,
      default: 0,
      validator: function (value) {
        return value >= 0 && value <= 9
      },
    },
    column5: {
      type: Number,
      default: 0,
      validator: function (value) {
        return value >= 0 && value <= 5
      },
    },
    column6: {
      type: Number,
      default: 0,
      validator: function (value) {
        return value >= 0 && value <= 9
      },
    },
  },
  mounted() {},
  filters: {
    columnClass: (column) => 'column-' + column
  }
}




