import THeader from "../../components/THeader";
export default {
  name: "profile",
  components: {
    THeader
  },
  data() {
    return {
      userInfo: {}
    };
  },
  computed: {},
  mounted() {
    this.getUserInfo();
  },
  methods: {
    goback() {
      this.$router.back();
    },
    navToLogin() {
      return;
      // this.$router.push({
      //   name: "login"
      // });
    },
    goProfile() {
      this.$router.push({
        name: "profile"
      });
    },
    getUserInfo() {
      let requestData = this.$store.getters.tokenInfo;
      this.$startRequest(
        "/user/getUserInfo",
        requestData,
        response => {
          this.userInfo = response.data.user_info;
        },
        error => {
          if (error.msg) {
            this.$vux.toast.text(error.msg, "top");
          } else {
            this.$vux.toast.text("网络错误", "top");
          }
        }
      );
    },
    logout() {
      let requestData = this.$store.getters.tokenInfo;
      this.$startRequest(
        "/user/getUserInfo",
        requestData,
        response => {
          this.userInfo = response.data.user_info;
        },
        error => {
          if (error.msg) {
            this.$vux.toast.text(error.msg, "top");
          } else {
            this.$vux.toast.text("网络错误", "top");
          }
        }
      );
    },
    confirmLogout() {
      this.$vux.confirm.show({
        title: "确认删除",
        content: "确定要删除该类别吗？删除后所属的所有事项与计划都会被删除~",
        onCancel: () => {},
        onConfirm: () => {
          this.logout(this.editingCategory);
        }
      });
    },
    logout() {
      let requestData = this.$store.getters.tokenInfo;
      this.$startRequest(
        "/user/logout",
        requestData,
        res => {
          this.$vux.toast.text("登出成功", "top");
          this.$router.push({
            name: "login"
          });
        },
        error => {
          if (error.msg) {
            this.$vux.toast.text(error.msg, "top");
          } else {
            this.$vux.toast.text("网络错误", "top");
          }
        }
      );
    }
  }
};



