
import Selector from "@/components/common/Selector";
import Label from "../../components/common/Label.vue";
import Glowing from "../../components/common/Glowing.vue";
import CircleMenu from "vue-circle-menu";
import { swiper, swiperSlide } from "vue-awesome-swiper";
import Popup from 'vux/src/components/popup/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

import Group from "@/components/common/Group";
import Cell from "@/components/common/Cell";
import { Slider } from "vue-color";
export default {
name: "management",
components: {
swiper,
swiperSlide,
Label,
Selector,
CircleMenu,
Group,
Popup,
Flexbox,
FlexboxItem,
Group,
Cell,
Slider,
Glowing
},
data() {
return {
stateList: [
{
value: 0,
name: "全部"
},
{
value: 1,
name: "待办"
},
{
value: 2,
name: "已完成"
}
],
stateSelected: {
value: 0,
name: "全部"
},
importanceLevelList: [
{
value: 0,
name: "全部"
},
{
value: 1,
name: "重要"
},
{
value: 2,
name: "不重要"
}
],
importanceLevelSelected: {
value: 0,
name: "全部"
},
emergencyLevelList: [
{
value: 0,
name: "全部"
},
{
value: 1,
name: "紧急"
},
{
value: 2,
name: "不紧急"
}
],
emergencyLevelSelected: {
value: 0,
name: "全部"
},
itemList: {},
typeSwiperIndex: 0,
swiper: {},
categoryList: {},
editingCategory: {
category_name: "",
category_color: "#888",
icon: {
icon_id: 1,
icon_order: 1,
icon_size: 100,
icon_src: "fa fa-tag",
icon_type: 1
}
},
iconList: [],
categoryPanel: false,
colors: {
hex: "#888"
},
disableSaveCategory: false
};
},
watch: {
colors: function(val) {
this.editingCategory.category_color = val.hex;
}
},
mounted() {
this.getFilteredItems();
this.getCategoryList();
this.getIconList();
this.swiper = this.$refs.tswiper.swiper;
},
filters: {
colorsCss: color => "color:" + color,
backgroundCss: background => " background:" + background
},
computed: {},
methods: {
goCreateItem() {
this.$router.push({
name: "createItem",
});
},
swiperTo(index) {
this.swiper.slideTo(index, 500, true);
},
goDetails(index) {
this.$router.push({
name: "itemDetails",
params: {
syncKey: index
}
});
},
bindStateSelect(item) {
this.stateSelected = item;
this.getFilteredItems();
},
bindImportanceLevelSelect(item) {
this.importanceLevelSelected = item;

this.getFilteredItems();
},
bindEmergencyLevelSelect(item) {
this.emergencyLevelSelected = item;
this.getFilteredItems();
},
getFilteredItems() {
let requestData = {
state: this.stateSelected.value,
importance_level: this.importanceLevelSelected.value,
emergency_level: this.emergencyLevelSelected.value,
...this.$store.getters.tokenInfo
};
this.$startRequest(
"/item/getFilteredItems",
requestData,
res => {
this.itemList = res.data;
},
error => {
if (error.msg) {
this.$vux.toast.text(error.msg, "top");
} else {
this.$vux.toast.text("网络错误", "top");
}
}
);
},
getCategoryList() {
let requestData = this.$store.getters.tokenInfo;
this.$startRequest(
"/category/getCategoryList",
requestData,
res => {
this.categoryList = res.data;
},
error => {
if (error.msg) {
this.$vux.toast.text(error.msg, "top");
} else {
this.$vux.toast.text("网络错误", "top");
}
}
);
},

getIconList() {
let requestData = this.$store.getters.tokenInfo;
this.$startRequest(
"/icon/getIconList",
requestData,
res => {
this.iconList = res.data;
},
error => {
if (error.msg) {
this.$vux.toast.text(error.msg, "top");
} else {
this.$vux.toast.text("网络错误", "top");
}
}
);
},
saveCategory() {
if (this.disableSaveCategory) {
return;
}

this.disableSaveCategory = true;
if (!this.editingCategory.category_name) {
this.$vux.toast.text("请填写类别名称", "top");
return;
}
this.$vux.loading.show({
text: "保存中"
});
let requestData = {
...this.$store.getters.tokenInfo,
...this.editingCategory
};
this.$startRequest(
"/category/saveCategory",
requestData,
res => {
this.categoryList = res.data;
this.$vux.loading.hide();
this.$vux.toast.text("保存成功", "top");
this.hideCategoryPanel();
this.disableSaveCategory = false;
},
error => {
this.$vux.loading.hide();
if (error.msg) {
this.$vux.toast.text(error.msg, "top");
} else {
this.$vux.toast.text("网络错误", "top");
}
this.disableSaveCategory = false;
}
);
},
createCategory() {
this.editingCategory = {
category_name: "",
category_color: "#888",
icon: {
icon_id: 1,
icon_order: 1,
icon_size: 100,
icon_src: "fa fa-tag",
icon_type: 1
},
category_icon_id: 1
};
this.categoryPanel = true;
},
setIconOfEditingCategory(icon) {
this.editingCategory.icon = icon;
this.editingCategory.category_icon_id = icon.icon_id;
},

editCategory(category) {
this.editingCategory = { ...category };
this.categoryPanel = true;
},

hideCategoryPanel() {
this.categoryPanel = false;
this.disableSaveCategory = false;
},
confirmDeleteCategory() {
this.$vux.confirm.show({
title: "确认删除",
content: "确定要删除该类别吗？删除后所属的所有事项与计划都会被删除~",
onCancel: () => {},
onConfirm: () => {
this.deleteCategory(this.editingCategory);
}
});
},
deleteCategory(category) {
if (!category.category_id) {
return;
}
let requestData = {
category_id: category.category_id,
...this.$store.getters.tokenInfo
};
this.$startRequest(
"/category/deleteCategory",
requestData,
res => {
this.$vux.toast.text("删除成功", "top");
this.getCategoryList();
this.hideCategoryPanel();
},
error => {
if (error.msg) {
this.$vux.toast.text(error.msg, "top");
} else {
this.$vux.toast.text("网络错误", "top");
}
}
);
}
},
directives: {
TransferDom
}
};



