export default {
  name: "user",
  components: {},
  data() {
    return {
      userInfo: {
        portrait: null
      }
    };
  },
  computed: {},
  mounted() {
    this.getUserInfo();
  },
  methods: {
    navToLogin() {
      return;
      // this.$router.push({
      //   name: "login"
      // });
    },
    goProfile() {
      this.$router.push({
        name: "profile"
      });
    },
    getUserInfo() {
      let requestData = this.$store.getters.tokenInfo;
      this.$startRequest(
        "/user/getUserInfo",
        requestData,
        response => {
          this.userInfo = response.data.user_info;
        },
        error => {
          if (error.msg) {
            this.$vux.toast.text(error.msg, "top");
          } else {
            this.$vux.toast.text("网络错误", "top");
          }
        }
      );
    },
    logout() {
      let requestData = this.$store.getters.tokenInfo;
      this.$startRequest(
        "/user/getUserInfo",
        requestData,
        response => {
          this.userInfo = response.data.user_info;
        },
        error => {
          if (error.msg) {
            this.$vux.toast.text(error.msg, "top");
          } else {
            this.$vux.toast.text("网络错误", "top");
          }
        }
      );
    },
    confirmLogout() {
      this.$vux.confirm.show({
        title: "确认退出",
        content: "确定要退出当前吗？退出后需要重新登录账号才能继续使用~",
        onCancel: () => {},
        onConfirm: () => {
          this.logout(this.editingCategory);
        }
      });
    },
    logout() {
      let requestData = this.$store.getters.tokenInfo;
      this.$startRequest(
        "/user/logout",
        requestData,
        res => {
          this.$vux.toast.text("登出成功", "top");
          this.$router.push({
            name: "login"
          });
        },
        error => {
          if (error.msg) {
            this.$vux.toast.text(error.msg, "top");
          } else {
            this.$vux.toast.text("网络错误", "top");
          }
        }
      );
    }
  }
};



