
import { mapGetters } from "vuex";
import Picker from 'vux/src/components/picker/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Range from 'vux/src/components/range/index.vue'

import THeader from "../../components/THeader";
import Label from "@/components/common/Label";
import TGroup from "@/components/common/Group";
import Cell from "@/components/common/Cell";
import TransferDom from 'vux/src/directives/transfer-dom/index.js'


export default {
name: "HelloWorld",

data() {
return {
showPopupPicker: false,
item: {},
defaultPicked: [1]
};
},
components: {
THeader,
Label,
PopupPicker,
Group,
TGroup,
Cell,
Range
},
mounted() {
this.$vux.loading.show({
text:''
});
this.getItem();
this.getCategoryList();
},
computed: {
...mapGetters(["categoryPickerList", "categoryMap", "tokenInfo"])
},
methods: {
getCategoryList() {
let requestData = this.tokenInfo;
this.$startRequest(
"/category/getCategoryList",
requestData,
res => {
this.$store.commit("setCategoryList", res.data);
},
error => {
if (error.msg) {
this.$vux.toast.text(error.msg, "top");
} else {
this.$vux.toast.text("网络错误", "top");
}
}
);
},
getItem() {
let item_sync_key = this.$route.params.syncKey;
if (!item_sync_key) {
this.$router.back();
}
let requestData = { item_sync_key, ...this.$store.getters.tokenInfo };
this.$startRequest(
"/item/getItem",
requestData,
res => {
this.item = res.data;
this.$vux.loading.hide();
},
error => {
this.$vux.loading.hide();
if (error.msg) {
this.$vux.toast.text(error.msg, "top");
} else {
this.$vux.toast.text("网络错误", "top");
}
}
);
},
updateItem() {
let requestData = { ...this.item, ...this.$store.getters.tokenInfo };
this.$startRequest(
"/item/updateItem",
requestData,
res => {
this.$vux.toast.text("保存成功", "top");
this.$router.back();
},
error => {
if (error.msg) {
this.$vux.toast.text(error.msg, "top");
} else {
this.$vux.toast.text("网络错误", "top");
}
}
);
},
goback() {
this.$router.back();
},
bindImportanceLevelActive() {
if (this.item.item_importance_level >= 5) {
this.item.item_importance_level = 2;
} else {
this.item.item_importance_level = 7;
}
},
bindEmergencyLevelActive() {
if (this.item.item_emergency_level >= 5) {
this.item.item_emergency_level = 2;
} else {
this.item.item_emergency_level = 7;
}
},
onCategoryPickerChange(val) {
this.item.category_id = val[0];
},
completeItem(item_sync_key) {
let item = this.todayTodoList[item_sync_key];
let requestData = { item_sync_key, ...this.$store.getters.tokenInfo };
this.$startRequest(
"/item/completeItem",
requestData,
res => {
this.item = res.data;
},
error => {
if (error.msg) {
this.$vux.toast.text(error.msg, "top");
} else {
this.$vux.toast.text("网络错误", "top");
}
}
);
},
confirmDeleteItem() {
this.$vux.confirm.show({
title: "确认删除",
content: "确定要删除吗？删除就没啦~",
onCancel: () => {},
onConfirm: () => {
this.deleteItem();
}
});
},
deleteItem() {
let item_sync_key = this.item.item_sync_key;
let requestData = { item_sync_key, ...this.$store.getters.tokenInfo };
this.$startRequest(
"/item/deleteItem",
requestData,
res => {
this.$vux.toast.text("删除成功", "top");
this.$router.back();
},
error => {
if (error.msg) {
this.$vux.toast.text(error.msg, "top");
} else {
this.$vux.toast.text("网络错误", "top");
}
}
);
},
completeItem() {
let item_sync_key = this.item.item_sync_key;
let requestData = { item_sync_key, ...this.tokenInfo };
this.$startRequest(
"/item/completeItem",
requestData,
res => {
this.item = res.data;
},
error => {
if (error.msg) {
this.$vux.toast.text(error.msg, "top");
} else {
this.$vux.toast.text("网络错误", "top");
}
}
);
},
restartItem() {
let item_sync_key = this.item.item_sync_key;
let requestData = { item_sync_key, ...this.$store.getters.tokenInfo };
this.$startRequest(
"/item/restartItem",
requestData,
res => {
this.item = res.data;
},
error => {
if (error.msg) {
this.$vux.toast.text(error.msg, "top");
} else {
this.$vux.toast.text("网络错误", "top");
}
}
);
}
},
directives: {
TransferDom
}
};



