
import { mapGetters, mapMutations } from "vuex";
import Label from "../../components/common/Label.vue";
import Swipeout from 'vux/src/components/swipeout/swipeout.vue'
import SwipeoutItem from 'vux/src/components/swipeout/swipeout-item.vue'
import SwipeoutButton from 'vux/src/components/swipeout/swipeout-button.vue'

export default {
name: "home",
components: {
Label,
Swipeout,
SwipeoutItem,
SwipeoutButton
},
data() {
return {
msg: "Welcome to Your Vue.js App",
show: true
};
},
methods: {},
computed: {
...mapGetters(["tokenInfo", "todayTodoList"])
},
mounted() {
this.getItemList();
},
methods: {
goDetails(index) {
this.$router.push({
name: "itemDetails",
params: {
syncKey: index
}
});
},
confirmDeleteItem(item_sync_key) {
this.$vux.confirm.show({
title: "确认删除",
content: "确定要删除吗？删除就没啦~",
onCancel: () => {},
onConfirm: () => {
this.deleteItem(item_sync_key);
}
});
},
deleteItem(item_sync_key) {
let requestData = { item_sync_key, ...this.$store.getters.tokenInfo };
this.$startRequest(
"/item/deleteItem",
requestData,
res => {
this.$vux.toast.text("删除成功", "top");
this.getItemList();
},
error => {
if (error.msg) {
this.$vux.toast.text(error.msg, "top");
} else {
this.$vux.toast.text("网络错误", "top");
}
}
);
},
completeItem(item_sync_key) {
let item = this.todayTodoList[item_sync_key];
let requestData = { item_sync_key, ...this.tokenInfo };
this.$startRequest(
"/item/completeItem",
requestData,
res => {
let newItem = {};
newItem[res.data.item_sync_key] = res.data;
this.addItemList(newItem);
},
error => {
if (error.msg) {
this.$vux.toast.text(error.msg, "top");
} else {
this.$vux.toast.text("网络错误", "top");
}
}
);
},
restartItem(item_sync_key) {
let item = this.todayTodoList[item_sync_key];
let requestData = { item_sync_key, ...this.tokenInfo };
this.$startRequest(
"/item/restartItem",
requestData,
res => {
let newItem = {};
newItem[res.data.item_sync_key] = res.data;
this.addItemList(newItem);
},
error => {
if (error.msg) {
this.$vux.toast.text(error.msg, "top");
} else {
this.$vux.toast.text("网络错误", "top");
}
}
);
},
getItemList() {
let requestData = this.tokenInfo;
this.$startRequest(
"/item/getTodoList",
requestData,
res => {
this.setItemList(res.data);
},
error => {
if (error.msg) {
this.$vux.toast.text(error.msg, "top");
} else {
this.$vux.toast.text("网络错误", "top");
}
}
);
},
...mapMutations(["addItemList", "setItemList"])
}
};



