export default {
  name: "Selector",
  data() {
    return {};
  },
  props: {
    options: {
      type: Array,
      default: () => []
    },
    selected: {
      type: Object,
      default: () => {}
    },
    label: {
      type: String,
      default: ""
    },
    selectedColor: {
      type: String,
      default: "#f87"
    }
  },
  filters: {
    selectedClass: isSelected => (isSelected ? "selected" : "")
  },
  computed: {},
  methods: {
    select(item) {
      this.$emit("on-select", item);
    }
  },
  mounted() {
  }
};



